body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  background-color: #f0f2f3; /* Light blue background for a fresh look */
}

.treasure-container {
  padding: 5rem;
  max-width: 100%;
  overflow-x: hidden;
  background: linear-gradient(180deg, rgb(159, 201, 248) 0%, rgb(59, 150, 246) 60%, rgba(66, 137, 213, 0.8) 100%);
}

.treasure-header {
  text-align: center;
  margin-bottom: 1.5rem;
}

.treasure-header h2 {
  font-size: 2.5rem;
  font-weight: bold;
  color: #0f172a;
}

.treasure-header p {
  color: #334155;
  max-width: 600px;
  margin: 0.5rem auto 0;
  font-size: 1.1rem;
}

.carousel-wrapper {
  position: relative;
  display: flex;
  align-items: center;
}

.carousel-content {
  display: flex;
  gap: 1.5rem;
  overflow-x: auto;
  scroll-behavior: smooth;
  padding: 1rem 0;
  scrollbar-width: none; /* Firefox */
}

.carousel-content::-webkit-scrollbar {
  display: none; /* Chrome/Safari */
}

.treasure-card {
  flex: 0 0 415px; /* fixed width for uniformity */
  background: #f3f1f3; /* consistent background */
  border-radius: 16px;
  overflow: hidden;
  box-shadow: 0 6px 15px rgba(0, 0, 0, 0.1);
  transition: transform 0.3s;
  display: flex;
  flex-direction: column;
}

.treasure-card:hover {
  transform: scale(1.03);
}

.treasure-image-wrapper {
  position: relative;
  height: 250px; /* fix height to ensure uniform card size */
}

.treasure-image {
  width: 100%;
  height: 100%;
  object-fit: cover;
  border-bottom: 1px solid #eee;
}

.treasure-placeholder {
  width: 100%;
  height: 100%;
  background: #cbd5e1;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 3rem;
  color: #475569;
}

.treasure-details {
  padding: 1rem;
  text-align: center;
  flex-grow: 1;
  background-color: #f3f1f3; /* ensures detail section matches image section */
}

.treasure-details h3 {
  margin: 0.5rem 0;
  font-size: 1.3rem;
  color: #1e293b;
}

.treasure-category {
  font-size: 1rem;
  color: #64748b;
}

.trade-btn {
  margin-top: 0.75rem;
  padding: 0.5rem 1.2rem;
  background-color: #0284c7;
  color: white;
  border: none;
  border-radius: 8px;
  cursor: pointer;
  font-size: 1rem;
  transition: background-color 0.2s;
}

.trade-btn:hover {
  background-color: #0369a1;
}

.scroll-btn {
  background: none;
  border: none;
  font-size: 2.5rem;
  cursor: pointer;
  color: #334155;
  padding: 0 10px;
}

.scroll-btn.left {
  margin-right: -10px;
}

.scroll-btn.right {
  margin-left: -10px;
}